/* ============================================================
   CLEAN ADMIN OVERRIDES (SAFE FOR DRUPAL 10/11)
   ============================================================ */

/* Light, readable admin UI only on admin routes */
body.path-admin {
  background: #f5f5f5 !important;
  color: #111 !important;
}

/* Restore core Drupal toolbar layout & colors */
.toolbar,
.toolbar-bar,
.toolbar-tray {
  background: #f5f5f5 !important;
  color: #222 !important;
  mix-blend-mode: normal !important;
  position: relative !important;
  z-index: 10000 !important;
  border: none !important;
  box-shadow: none !important;
}

/* Make sure the toolbar is NOT pushed off-screen */
body.toolbar-fixed {
  margin-top: 39px !important;
}

body.toolbar-fixed.toolbar-tray-open {
  margin-top: 79px !important;
}

/* Ensure toolbar text/icons visible */
.toolbar a,
.toolbar button,
.toolbar-tray a,
.toolbar-tray button {
  color: #222 !important;
  fill: #222 !important;
  text-shadow: none !important;
  opacity: 1 !important;
  background: transparent !important;
}

/* Prevent your theme's z-index from covering toolbar */
body,
#page-wrapper,
header,
main,
footer {
  position: relative !important;
  z-index: 1 !important;
}

/* Remove dark theme affecting admin edit forms */
body.path-admin * {
  background: none !important;
  color: inherit !important;
}
